.pul-preview-panel {
  position: relative;
  width: 100%;
  text-align: center;
  background-color: #f5f4f3;
  overflow: hidden;
}
.pul-preview-panel__image {
  max-width: 100%;
  min-width: 124px;
  height: auto;
  max-height: 100%;
}
.pul-preview-panel__html {
  min-width: 124px;
  height: 200px;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  overflow: hidden;
}
.pul-preview-panel__html iframe {
  width: 100%;
  height: 100%;
}
.pul-preview-panel__html--loading {
  position: relative;
}
.pul-preview-panel__html--loading iframe {
  display: none;
}
.pul-preview-panel__content-blocker {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.pul-preview-panel__loader {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.pul-preview-panel__loader-spinner {
  width: 32px;
  height: 32px;
}
.pul-preview-panel__content {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 20px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.7);
  transition: 0.3s;
  opacity: 1;
}
.pul-preview-panel__content--empty {
  background-color: rgba(0, 0, 0, 0);
}
.pul-preview-panel--background {
  background-repeat: no-repeat;
  background-position: center;
  min-width: 124px;
  min-height: 200px;
}
.pul-preview-panel:not(:hover) > .pul-preview-panel__content {
  visibility: hidden;
  opacity: 0;
}
@-webkit-keyframes pul-preview-panel--spin {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes pul-preview-panel--spin {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



