.pul-auxiliary-actions,
.pul-auxiliary-actions a {
  color: #7aa9bf;
}
.pul-auxiliary-actions a:hover {
  color: #94c2d8;
}
.pul-auxiliary-actions::before {
  content: "[";
}
.pul-auxiliary-actions::after {
  content: "]";
}
.pul-auxiliary-actions__separator {
  font-size: 10px;
  vertical-align: 1px;
}



