.pul-panel {
  margin: 0 0 12px;
  border: 1px solid #dedede;
  background-color: #fafafa;
}
.pul-panel__header {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  min-height: 24px;
  margin: -1px;
  padding: 5px 11px;
  background-color: #dedede;
}
.pul-panel--collapsible .pul-panel__header {
  cursor: pointer;
}
.pul-panel__title {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  margin: 0;
  padding: 0;
  line-height: 18px;
  font-size: 15px;
  color: #303030;
}
.pul-panel__control-button {
  margin-left: auto;
  -ms-flex-item-align: start;
      align-self: flex-start;
}
.pul-panel__title + .pul-panel__control-button {
  margin-left: 5px;
}
.pul-panel__content {
  padding: 10px;
  color: #4a4a4a;
  line-height: normal;
  word-wrap: break-word;
  -webkit-animation: pul-panel--fade 0.3s ease-in-out;
          animation: pul-panel--fade 0.3s ease-in-out;
}
.pul-panel--collapsed .pul-panel__content {
  display: none;
}
@-webkit-keyframes pul-panel--fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes pul-panel--fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}



