.pul-badge {
  position: relative;
  display: inline-block;
}
.pul-badge__value {
  position: absolute;
  top: 0;
  right: 10px;
  -webkit-transform: translateY(-50%) translateX(100%);
          transform: translateY(-50%) translateX(100%);
  padding: 1px 6px;
  z-index: 1;
}



