/*
Button Group

Markup:
<div class="button-group">
  <button class="button" type="button">Button</a>
  <button class="button" type="button">Button</a>
  <div class="button-group">
    <button class="button" type="button">Button</a>
    <button class="button" type="button">Button</a>
  </div>
</div>
*/
.pul-button-group {
  position: relative;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  vertical-align: middle;
}
.pul-button-group > .pul-button {
  position: relative;
  -webkit-box-flex: 0;
      -ms-flex: 0 1 auto;
          flex: 0 1 auto;
  margin-bottom: 0;
}
.pul-button-group > .pul-button:hover,
.pul-button-group > .pul-button:active,
.pul-button-group > .pul-button:focus,
.pul-button-group > .pul-button--active,
.pul-button-group > .pul-button--hovered {
  z-index: 2;
}
.pul-button-group .pul-button + .pul-button,
.pul-button-group .pul-button + .pul-button-group,
.pul-button-group .pul-button-group + .pul-button,
.pul-button-group .pul-button-group + .pul-button-group {
  margin-left: 1px;
}
.pul-button-group > .pul-button:not(:first-child):not(:last-child) {
  border-radius: 0;
}
.pul-button-group > .pul-button:first-child {
  margin-left: 0;
}
.pul-button-group > .pul-button:first-child:not(:last-child),
.pul-button-group > .pul-button-group:first-child:not(:last-child) > .pul-button:last-child {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.pul-button-group > .pul-button:last-child:not(:first-child),
.pul-button-group > .pul-button-group:last-child:not(:first-child) > .pul-button:first-child {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.pul-button-group > .pul-button-group:not(:first-child):not(:last-child) > .pul-button {
  border-radius: 0;
}



