/*
Popover

Markup:
<div class="popover {.modifier}">
    <div class="popover__inner">
        <div class="popover__heading">
            <button class="icon-button popover__close {.modifier}"><span class="icon">...</span></button>
            <h4 class="popover__title">Popup Title</h4>
        </div>
        <div class="popover__content">
            <button class="icon-button popover__close"><span class="icon"><svg>...</svg></span></button>
            <p>Content</p>
        </div>
    </div>
    <span class="popover__arrow {.modifier}"></span>
</div>

Modifiers:
.popover--placement-top
.popover--placement-top-start
.popover--placement-top-end
.popover--placement-left
.popover--placement-left-start
.popover--placement-left-end
.popover--placement-bottom
.popover--placement-bottom-start
.popover--placement-bottom-end
.popover--placement-right
.popover--placement-right-start
.popover--placement-right-end

.popover__close--heading
.popover__arrow--heading
*/
.pul-popover {
  position: absolute;
  max-width: 400px;
  font-size: 13px;
  text-align: left;
  color: #000;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #cccccc;
  white-space: normal;
  opacity: 1;
  -webkit-animation: pul-popover--fade 0.3s ease-in-out;
          animation: pul-popover--fade 0.3s ease-in-out;
}
.pul-popover__target {
  cursor: pointer;
}
.pul-popover__arrow,
.pul-popover__arrow::after {
  position: absolute;
  display: block;
  width: 0;
  height: 0;
  border: solid transparent;
}
.pul-popover__arrow {
  border-width: 0 8px 8px;
}
.pul-popover__arrow::after {
  content: "";
  top: 1px;
  margin-left: -8px;
  border-width: 0 8px 8px;
}
.pul-popover__arrow:not(.pul-popover__arrow--heading) {
  border-bottom-color: #cccccc;
}
.pul-popover__arrow:not(.pul-popover__arrow--heading)::after {
  border-bottom-color: #fff;
}
.pul-popover__arrow--heading {
  border-bottom-color: #1e96c9;
}
.pul-popover__arrow--heading::after {
  border-bottom-color: #1e96c9;
}
.pul-popover__heading {
  position: relative;
  margin: -1px -1px 0;
  padding: 8px 30px 8px 10px;
  background-color: #1e96c9;
  border: 1px solid #1e96c9;
  white-space: nowrap;
}
.pul-popover__title {
  margin: 0;
  padding: 0;
  overflow: hidden;
  text-overflow: ellipsis;
}
.pul-popover__heading,
.pul-popover__title {
  font-size: 13px;
  line-height: 1.4;
  color: #fff;
}
.pul-popover__close {
  float: right;
  min-height: 18px;
  min-width: 18px;
  margin: 4px;
  padding: 1px;
}
.pul-popover__close--heading {
  color: currentColor;
  margin-top: 0;
  margin-right: -22px;
}
.pul-popover__content {
  box-sizing: border-box;
  padding: 10px;
}
.pul-popover__content > :last-child {
  margin-bottom: 0;
}
.pul-popover__inner {
  outline: none;
}
.pul-popover--placement-top-start,
.pul-popover--placement-top,
.pul-popover--placement-top-end {
  margin-bottom: 10px;
}
.pul-popover--placement-top-start .pul-popover__arrow,
.pul-popover--placement-top .pul-popover__arrow,
.pul-popover--placement-top-end .pul-popover__arrow {
  bottom: -8px;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  margin: 0 10px;
}
.pul-popover--placement-right-start,
.pul-popover--placement-right,
.pul-popover--placement-right-end {
  margin-left: 10px;
}
.pul-popover--placement-right-start .pul-popover__arrow,
.pul-popover--placement-right .pul-popover__arrow,
.pul-popover--placement-right-end .pul-popover__arrow {
  left: -12px;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  margin: 10px 0;
}
.pul-popover--placement-bottom-start,
.pul-popover--placement-bottom,
.pul-popover--placement-bottom-end {
  margin-top: 10px;
}
.pul-popover--placement-bottom-start .pul-popover__arrow,
.pul-popover--placement-bottom .pul-popover__arrow,
.pul-popover--placement-bottom-end .pul-popover__arrow {
  top: -8px;
  margin: 0 10px;
}
.pul-popover--placement-left-start,
.pul-popover--placement-left,
.pul-popover--placement-left-end {
  margin-right: 10px;
}
.pul-popover--placement-left-start .pul-popover__arrow,
.pul-popover--placement-left .pul-popover__arrow,
.pul-popover--placement-left-end .pul-popover__arrow {
  right: -12px;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  margin: 10px 0;
}
.pul-popover--intent-success {
  background-color: #e1f1cf;
  border-color: #b0d980;
}
.pul-popover--intent-success .pul-popover__arrow:not(.pul-popover__arrow--heading) {
  border-bottom-color: #b0d980;
}
.pul-popover--intent-success .pul-popover__arrow:not(.pul-popover__arrow--heading)::after {
  border-bottom-color: #e1f1cf;
}
.pul-popover--intent-info {
  background-color: #ddf0fc;
  border-color: #80c5f4;
}
.pul-popover--intent-info .pul-popover__arrow:not(.pul-popover__arrow--heading) {
  border-bottom-color: #80c5f4;
}
.pul-popover--intent-info .pul-popover__arrow:not(.pul-popover__arrow--heading)::after {
  border-bottom-color: #ddf0fc;
}
.pul-popover--intent-warning {
  background-color: #feebc8;
  border-color: #fcc564;
}
.pul-popover--intent-warning .pul-popover__arrow:not(.pul-popover__arrow--heading) {
  border-bottom-color: #fcc564;
}
.pul-popover--intent-warning .pul-popover__arrow:not(.pul-popover__arrow--heading)::after {
  border-bottom-color: #feebc8;
}
.pul-popover--intent-danger {
  background-color: #fadbdb;
  border-color: #ed8282;
}
.pul-popover--intent-danger .pul-popover__arrow:not(.pul-popover__arrow--heading) {
  border-bottom-color: #ed8282;
}
.pul-popover--intent-danger .pul-popover__arrow:not(.pul-popover__arrow--heading)::after {
  border-bottom-color: #fadbdb;
}
@media (max-width: 480px) {
  .pul-popover {
    left: 10px !important;
    right: 10px;
    max-width: none;
  }
}
@-webkit-keyframes pul-popover--fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes pul-popover--fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}



