/*
Switch

Markup:
<label class="switch {{.modifier}}">
    <span class="switch__label">Text</span>
    <input type="checkbox" :modifier />
    <span class="switch__indicator" />
</label>

Modifiers:
.switch--warning
.switch--danger
.switch--loading
:disabled
*/
.pul-switch {
  cursor: pointer;
  position: relative;
  display: inline-block;
  height: 26px;
}
.pul-switch label {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.pul-switch__label {
  display: inline-block;
  margin-right: 5px;
  vertical-align: middle;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.pul-switch__indicator {
  box-sizing: border-box;
  cursor: pointer;
  display: inline-block;
  position: relative;
  height: 26px;
  width: 56px;
  color: #979797;
  font-size: 10px;
  font-weight: 700;
  line-height: 20px;
  text-align: center;
  background-color: #dedede;
  border: 2px solid #dedede;
  text-transform: uppercase;
  border-radius: 13px;
  vertical-align: middle;
  transition: 0.3s ease-out;
}
.pul-switch__indicator::before {
  content: "";
  position: absolute;
  top: 5px;
  left: 33px;
  height: 8px;
  width: 8px;
  border: 2px solid #979797;
  border-radius: 50%;
}
.pul-switch__indicator::after {
  position: relative;
  content: "";
  display: block;
  width: 18px;
  height: 18px;
  margin: 2px;
  background-color: #fff;
  border-radius: 50%;
  box-shadow: 0 2px 1px rgba(0, 0, 0, 0.2);
}
.pul-switch input {
  -webkit-appearance: none;
  position: absolute;
  width: inherit;
  height: inherit;
  opacity: 0;
  left: 0;
  top: 0;
  z-index: -1;
}
.pul-switch input:checked ~ .pul-switch__indicator {
  border-color: #81ca0d;
  background-color: #81ca0d;
  padding-left: 30px;
}
.pul-switch input:checked ~ .pul-switch__indicator::before {
  content: "";
  position: absolute;
  top: 5px;
  left: 12px;
  height: 12px;
  width: 0;
  border-width: 0 2px 0 0;
  border-color: #fff;
  border-radius: 0;
}
.pul-switch input:not(:disabled):focus {
  outline: none;
}
.pul-switch input:not(:disabled):focus ~ .pul-switch__indicator {
  border-color: #d1d1d1;
  background-color: #d1d1d1;
}
.pul-switch input:not(:disabled):focus:checked + .pul-switch__indicator {
  border-color: #72b20b;
  background-color: #72b20b;
}
.pul-switch input:disabled ~ .pul-switch__indicator {
  border-color: #e8e8e8;
  background-color: #e8e8e8;
  cursor: not-allowed;
}
.pul-switch input:disabled ~ .pul-switch__indicator::after {
  box-shadow: none;
  background-color: rgba(255, 255, 255, 0.8);
}
.pul-switch input:disabled ~ .pul-switch__indicator::before {
  opacity: 0.6;
}
.pul-switch input:disabled:checked ~ .pul-switch__indicator {
  border-color: #c1e18f;
  background-color: #c1e18f;
}
.pul-switch--warning input:checked ~ .pul-switch__indicator {
  border-color: #f9c97b;
  background-color: #f9c97b;
}
.pul-switch--warning input:not(:disabled):checked:focus + .pul-switch__indicator {
  border-color: #f5a622;
  background-color: #f5a622;
}
.pul-switch--warning input:disabled:checked ~ .pul-switch__indicator {
  border-color: #f7dcb0;
  background-color: #f7dcb0;
}
.pul-switch--danger input:checked ~ .pul-switch__indicator {
  border-color: #f57979;
  background-color: #f57979;
}
.pul-switch--danger input:not(:disabled):focus:checked + .pul-switch__indicator {
  border-color: #f01f1f;
  background-color: #f01f1f;
}
.pul-switch--danger input:disabled:checked ~ .pul-switch__indicator {
  border-color: #f4adad;
  background-color: #f4adad;
}
.pul-switch--loading .pul-switch__indicator {
  background-image: linear-gradient(-45deg, rgba(0, 0, 0, 0.15) 25%, transparent 25%, transparent 50%, rgba(0, 0, 0, 0.15) 50%, rgba(0, 0, 0, 0.15) 75%, transparent 75%, transparent);
  background-size: 22px 22px;
  -webkit-animation: pul-switch--stripes 0.5s linear infinite;
          animation: pul-switch--stripes 0.5s linear infinite;
}
.objects-toolbar > .pul-switch {
  float: left;
  margin: 1px 1px 10px 0;
}
.pul-menu .pul-switch__indicator,
.pul-menu input:not(:disabled):focus ~ .pul-switch__indicator {
  background-color: #999;
  border-color: #999;
}
@-webkit-keyframes pul-switch--stripes {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 22px 0 /*rtl:right 30px top 0*/;
  }
}
@keyframes pul-switch--stripes {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 22px 0 /*rtl:right 30px top 0*/;
  }
}
/*
