}
.pul-icon--size-16 {
  width: 16px;
  height: 16px;
}
.pul-icon--size-24 {
  width: 24px;
  height: 24px;
}
.pul-icon--size-32 {
  width: 32px;
  height: 32px;
}
.pul-icon--size-48 {
  width: 48px;
  height: 48px;
}
.pul-icon--size-64 {
