}
.pul-icon--size-96 {
  width: 96px;
  height: 96px;
}
.pul-icon--size-128 {
  width: 128px;
  height: 128px;
}
.pul-icon--size-192 {
  width: 192px;
  height: 192px;
}
.pul-icon--animation-spin {
  -webkit-animation: pul-icon--spin 1.4s linear infinite;
          animation: pul-icon--spin 1.4s linear infinite;
}
.pul-icon--rotate-90 {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
.pul-icon--rotate-180 {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
.pul-icon--rotate-270 {
  -webkit-transform: rotate(270deg);
