/*
Icon

Markup:
<span class="icon {{.modifier}}">{{children}}</span>
<span class="icon {{.modifier}}"></span>
<span class="icon {{.modifier}}"><img src="..." alt="..."></span>
<span class="icon {{.modifier}}"><svg>...</svg></span>

Modifiers:
.icon--size-16
.icon--size-24
.icon--size-32
.icon--size-48
.icon--size-64
.icon--size-96
.icon--size-128
.icon--size-192

.icon--animation-spin

.icon--rotate-90
.icon--rotate-180
.icon--rotate-270

.icon--flip-horizontal
.icon--flip-vertical
*/
.pul-icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  vertical-align: -3px;
  background-repeat: no-repeat;
  background-size: contain;
}
.pul-icon img,
.pul-icon svg {
  display: block;
  width: 100%;
  height: 100%;
  overflow: hidden;
  fill: currentColor;
}
.pul-icon--size-16 {
  width: 16px;
  height: 16px;
}
.pul-icon--size-24 {
  width: 24px;
  height: 24px;
}
.pul-icon--size-32 {
  width: 32px;
  height: 32px;
}
.pul-icon--size-48 {
  width: 48px;
  height: 48px;
}
.pul-icon--size-64 {
  width: 64px;
  height: 64px;
}
.pul-icon--size-96 {
  width: 96px;
  height: 96px;
}
.pul-icon--size-128 {
  width: 128px;
  height: 128px;
}
.pul-icon--size-192 {
  width: 192px;
  height: 192px;
}
.pul-icon--animation-spin {
  -webkit-animation: pul-icon--spin 1.4s linear infinite;
          animation: pul-icon--spin 1.4s linear infinite;
}
.pul-icon--rotate-90 {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
.pul-icon--rotate-180 {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
.pul-icon--rotate-270 {
  -webkit-transform: rotate(270deg);
          transform: rotate(270deg);
}
.pul-icon--flip-horizontal {
  -webkit-transform: scaleX(-1);
          transform: scaleX(-1);
}
.pul-icon--flip-vertical {
  -webkit-transform: scaleY(-1);
          transform: scaleY(-1);
}
@-webkit-keyframes pul-icon--spin {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes pul-icon--spin {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



