/*
Subnav

Markup:
<div class="subnav">
    <a class="subnav__item {{.modifier}}" href="...">...</a>
</div>

Modifiers:
.subnav__item--active
*/
.pul-subnav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: wrap;
      flex-flow: wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  margin: 0 0 16px 1px;
}
.pul-subnav__item {
  position: relative;
  -webkit-box-flex: 1;
      -ms-flex: 1 1 40%;
          flex: 1 1 40%;
  margin: -1px 0 0 -1px;
  padding: 5px;
  overflow: hidden;
  text-overflow: ellipsis;
  border: 1px solid #1e96c9;
  background-color: #fff;
  font: inherit;
  color: #373737;
  text-transform: none;
  display: inline-block;
  box-sizing: border-box;
  vertical-align: middle;
  min-height: 30px;
  line-height: 18px;
  font-size: 13px;
  text-decoration: none;
  text-align: center;
  white-space: nowrap;
  cursor: pointer;
  -webkit-appearance: none;
}
.pul-subnav__item:hover {
  border-color: transparent;
  background-color: #1e96c9;
  color: #fff;
  text-decoration: none;
}
.pul-subnav__item:active {
  background-color: #0e78a6;
  color: #fff;
}
.pul-subnav__item--active,
.pul-subnav__item--active:active,
.pul-subnav__item--active:hover {
  border-color: #1e96c9;
  background-color: #aed9eb;
  color: #373737;
}
@media (min-width: 768px) {
  .pul-subnav {
    -ms-flex-flow: nowrap;
        flex-flow: nowrap;
    margin: 0 0 16px;
  }
  .pul-subnav__item {
    -webkit-box-flex: 0;
        -ms-flex: 0 1 180px;
            flex: 0 1 180px;
    width: auto;
    text-align: center;
  }
}



