.pul-item-list__item {
  padding: 8px;
  cursor: pointer;
}
.pul-item-list__item .pul-item__title {
  color: #2498e3;
  text-decoration: none;
}
.pul-item-list__item:hover {
  background: #ecf5f9;
}
.pul-item-list__item:hover .pul-item__title {
  color: #188dd9;
  text-decoration: underline;
}
.pul-item-list__item--reorderable {
  margin: -2px;
  border: 2px solid #67ccff;
  border-radius: 3px;
  background-color: #e4f1f7;
  background-clip: padding-box;
  cursor: move;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.pul-item-list__item--reorderable img {
  pointer-events: none;
}
.pul-item-list__col--gap-xs {
  padding-left: 8px;
  padding-bottom: 8px;
}
.pul-item-list__col--gap-sm {
  padding-left: 12px;
  padding-bottom: 12px;
}
.pul-item-list__col--gap-md {
  padding-left: 16px;
  padding-bottom: 16px;
}
.pul-item-list__col--gap-lg {
  padding-left: 24px;
  padding-bottom: 24px;
}
.pul-item-list__col--gap-xl {
  padding-left: 32px;
  padding-bottom: 32px;
}
.pul-item-list .pul-item-list__item--reorderable {
  border-style: dashed;
}



