.pul-toolbar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.pul-toolbar__group {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  white-space: nowrap;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.pul-toolbar__group:not(:last-child) {
  margin-right: 10px;
}
.pul-toolbar__group-item:not(:last-child) {
  margin-right: 1px;
}
.pul-toolbar__expander {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}



