/*
List (Data Table)

Markup:
<table class="table">
  <thead>
    <tr>
      <th {{.modifier}}></th>
      <th {{.modifier}}>{{if is-sorted <div class="th__action">{{Name}}</div>}}</th>
    </tr>
  </thead>
  <tbody>
    <tr {{.modifier}}>
      <td {{.modifier}}></td>
      <td {{.modifier}}></td>
    </tr>
  </tbody>
</table>

Modifiers for <tr>:
.is-selected

Modifiers for <th>, <td>:
.is-sortable
.is-sorted--up
.is-sorted--down
.cell-nowrap
.cell-shrink
*/
.pul-list {
  width: 100%;
  max-width: 100%;
}
.pul-list th,
.pul-list td {
  padding: 6px 8px;
  vertical-align: top;
  border-top: 1px solid #d6d6d6;
}
.pul-list th {
  font-weight: 400;
  text-align: left;
  white-space: nowrap;
}
.pul-list thead th {
  vertical-align: bottom;
  border-bottom: 2px solid #d6d6d6;
}
.pul-list tbody + tbody {
  border-top: 2px solid #d6d6d6;
}
.pul-list tr:first-child > th,
.pul-list tr:first-child > td {
  border-top: 0;
}
.pul-list .cell-nowrap {
  white-space: nowrap;
}
.pul-list .cell-shrink {
  width: 1%;
}
.pul-list tbody tr.is-selected > td,
.pul-list tbody tr.is-selected > th {
  background-color: #ffdfba;
}
.pul-list tbody tr:hover > td,
.pul-list tbody tr:hover > th {
  box-shadow: #d8dde6 0 -1px 0 inset, #d8dde6 0 1px 0 inset;
}
.pul-list tbody tr:not(.is-selected):hover > td,
.pul-list tbody tr:not(.is-selected):hover > th {
  background-color: #ecf5f9;
}
.pul-list .is-sortable {
  position: relative;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.pul-list .is-sorted .th__action::after {
  position: absolute;
  display: inline-block;
  margin: 1px 0 0 2px;
  font-family: Consolas, monospace;
  color: #999;
}
.pul-list .is-sorted:hover .th__action::after {
  color: #000;
}
.pul-list .is-sorted--up .th__action::after {
  content: "\2191";
}
.pul-list .is-sorted--down .th__action::after {
  content: "\2193";
}



