.pul-icon-button {
  display: inline-block;
  overflow: visible;
  box-sizing: border-box;
  min-height: 24px;
  min-width: 24px;
  margin: 0;
  padding: 4px;
  cursor: pointer;
  text-align: center;
  vertical-align: top;
  color: #616161;
  border: none;
  border-radius: 2px;
  background-color: transparent;
  font: inherit;
  font-size: 13px;
  line-height: 1;
}
.pul-icon-button::-moz-focus-inner {
  border: 0;
  padding: 0;
}
.pul-icon-button:hover,
.pul-icon-button:focus {
  text-decoration: none;
  outline: none;
  background-color: rgba(0, 0, 0, 0.04);
}
.pul-icon-button:active {
  background-color: rgba(0, 0, 0, 0.14);
}



