.pul-tooltip {
  position: absolute;
  z-index: 1040;
  max-width: 240px;
  padding: 5px;
  text-align: left;
  word-wrap: break-word;
  border: 1px solid #aaa;
  font-size: 12px;
  background: #ffc;
  color: #000;
}
.pul-tooltip b,
.pul-tooltip i {
  font-weight: normal;
  font-style: normal;
}
.touch .pul-tooltip {
  display: none !important;
}



