.pul-form-field {
  box-sizing: border-box;
  min-height: 26px;
}
.pul-form-field--error {
  position: relative;
  background: #fedddf;
  box-shadow: 0 0 0 3px #fedddf;
}
.pul-form-field__error,
.pul-form-field__description {
  display: block;
  max-width: 500px;
  margin: 0 0 5px;
  padding: 0;
}
.pul-form-field__error {
  color: #c80000;
}



