.pul-status--danger,
.pul-status--danger .pul-action__icon {
  color: #e12d2d;
}
.pul-status--warning,
.pul-status--warning .pul-action__icon {
  color: #f59f05;
}
.pul-status--success,
.pul-status--success .pul-action__icon {
  color: #71a832;
}
.pul-status--info,
.pul-status--info .pul-action__icon {
  color: #39a5ee;
}
.pul-status--muted,
.pul-status--muted .pul-action__icon {
  color: #969696;
}



