/*
Button

Markup:
<button class="button .modifier" type="button" :modifier>Link</button>
<a class="button .modifier" role="button" tabindex="0">Button</a>

Modifiers:
.button--primary
.button--secondary
.button--success
.button--warning
.button--danger
.button--info

.button--active
.button--loading
.button--hovered
.button--disabled
*/
.pul-button {
  display: inline-block;
  overflow: visible;
  box-sizing: border-box;
  min-height: 30px;
  min-width: 30px;
  margin: 0;
  padding: 5px 8px;
  cursor: pointer;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
  text-decoration: none;
  text-transform: none;
  color: #373737;
  border: 1px solid transparent;
  border-radius: 0;
  background-color: #dedede;
  font: inherit;
  font-size: 13px;
  line-height: 18px;
}
.pul-button::-moz-focus-inner {
  border: 0;
  padding: 0;
}
.pul-button:hover,
.pul-button:focus,
.pul-button.pul-button--hovered {
  text-decoration: none;
  color: #fff;
  outline: none;
  background-color: #1e96c9;
}
.pul-button:hover .pul-button__icon,
.pul-button:focus .pul-button__icon,
.pul-button.pul-button--hovered .pul-button__icon {
  color: currentColor;
}
.pul-button:active,
.pul-button.pul-button--active {
  color: #fff;
  background-color: #0e78a6;
}
.pul-button:active .pul-button__icon,
.pul-button.pul-button--active .pul-button__icon {
  color: currentColor;
}
.pul-button--primary {
  background-color: #0f7faf;
  color: #fff;
}
.pul-button--primary .pul-button__icon {
  color: currentColor;
}
.pul-button--primary:hover,
.pul-button--primary:focus,
.pul-button--primary.pul-button--hovered {
  color: #fff;
  background-color: #1e96c9;
}
.pul-button--primary:active,
.pul-button--primary.pul-button--active {
  color: #fff;
  background-color: #0e78a6;
}
.pul-button--secondary {
  border-color: #2498e3;
  background-color: #fff;
  color: #2498e3;
}
.pul-button--secondary .pul-button__icon {
  color: currentColor;
}
