.pul-button--danger:active,
.pul-button--danger.pul-button--active {
  color: #373737;
  background-color: #ea7070;
}
.pul-button--info {
  background-color: #c6e5fa;
  color: #373737;
}
.pul-button--info .pul-button__icon {
  color: #39a5ee;
}
.pul-button--info:hover,
.pul-button--info:focus,
.pul-button--info.pul-button--hovered {
  color: #373737;
  background-color: #97d0f6;
}
.pul-button--info:hover .pul-button__icon,
.pul-button--info:focus .pul-button__icon,
