  border-color: #707070;
  color: #fff;
  background-color: #707070;
}
.pul-button[disabled],
.pul-button--disabled,
.pul-button--loading,
.pul-button[disabled]:hover,
.pul-button--disabled:hover,
.pul-button--loading:hover,
.pul-button[disabled]:focus,
.pul-button--disabled:focus,
.pul-button--loading:focus,
.pul-button[disabled]:active,
.pul-button--disabled:active,
.pul-button--loading:active {
  color: #b9b9b9;
  background-color: #e8e8e8;
  cursor: not-allowed;
}
.pul-button[disabled] .pul-button__icon,
.pul-button--disabled .pul-button__icon,
.pul-button[disabled] .pul-icon,
.pul-button--disabled .pul-icon {
  opacity: 0.7;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}
.pul-button--loading {
  position: relative;
  cursor: default;
  pointer-events: none;
}
.pul-button--loading .pul-button__spinner {
  width: 16px;
  height: 16px;
  margin-right: 6px;
  vertical-align: -3px;
}
.pul-button--loading .pul-button__icon {
  display: none;
}
.pul-button__icon {
  margin-right: 6px;
  color: #969696;
}
.pul-button__caret {
  display: inline-block;
  width: 0;
  height: 0;
  margin-left: 6px;
  vertical-align: 2px;
  border-top: 4px solid;
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
}
.pul-button--empty {
  padding: 5px;
}
.pul-button--empty .pul-button__icon,
.pul-button--empty .pul-button__spinner,
.pul-button--empty .pul-icon {
  margin: 0;
}
.pul-button--empty.pul-button--loading::before {
  margin-right: 0;
}
.pul-button--empty .pul-button__caret {
  margin-left: 0;
}
.pul-button--empty .pul-button__icon + .pul-button__caret,
.pul-button--empty .pul-button__spinner + .pul-button__caret {
  margin-left: 6px;
}



