.pul-section {
  position: relative;
  margin-bottom: 12px;
}
.pul-section + .pul-section {
  margin-top: 18px;
}
.pul-section__header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  margin-bottom: 8px;
  padding: 2px 0;
  border-bottom: 1px solid #ddd;
}
.pul-section__title {
  -webkit-box-flex: 1;
      -ms-flex: auto;
          flex: auto;
  margin: 0;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  color: #444;
}
.pul-section__control-button {
  margin: -2px 0;
}
.pul-section__control-button--collapse {
  margin-right: 8px;
}
.pul-section__title .pul-section__control-button,
.pul-section__header-buttons .pul-section__control-button {
  margin-left: 4px;
}
.pul-section__title .pul-section__control-button--edit .pul-section__control-button-icon {
  width: 14px;
  height: 14px;
}
.pul-section--danger {
  background-color: #fdeeed;
  box-shadow: 0 0 0 10px #fdeeed;
}
.pul-section--warning {
  background-color: #fef8ee;
  box-shadow: 0 0 0 10px #fef8ee;
}
.pul-section--success {
  background-color: #f5fdee;
  box-shadow: 0 0 0 10px #f5fdee;
}
.pul-section--inactive {
  background-color: #fdfcfc;
  box-shadow: 0 0 0 10px #fdfcfc;
}
.pul-section--inactive .pul-section__inner {
  opacity: 0.4;
}
.pul-section__title-edit {
  position: relative;
  display: inline-block;
  width: 50%;
}
.pul-section__title-edit input {
  margin: -3px 0 -3px -2px;
  width: 100%;
}
.pul-section__title-edit-actions {
  position: absolute;
  right: -2px;
  top: calc(100% + 3px);
  color: #000;
}



