/*
Plaintext

Markup:
<pre class="plaintext">{{children}}</pre>
*/
pre.pul-plaintext {
  background: transparent;
  margin: 0;
  padding: 0;
  overflow-wrap: break-word;
  white-space: pre-wrap;
  word-wrap: break-word;
  line-height: inherit;
  font-family: inherit;
  font-size: inherit;
  color: inherit;
}



