.pul-checkbox {
  display: inline-block;
  white-space: nowrap;
}
.pul-checkbox__input {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
}
.pul-checkbox__indicator {
  position: relative;
  display: inline-block;
  width: 16px;
  height: 16px;
  vertical-align: top;
  border: 1px solid #b9b9b9;
  border-radius: 2px;
  background-color: #fff;
  transition: border 0.1s, background-color 0.1s;
  cursor: pointer;
}
.pul-checkbox__text {
  margin-left: 8px;
  line-height: 16px;
  white-space: normal;
}
.pul-checkbox:hover .pul-checkbox__input ~ .pul-checkbox__indicator,
.pul-checkbox__input:focus ~ .pul-checkbox__indicator {
  border-color: #76c8f4;
}
.pul-checkbox__input:checked ~ .pul-checkbox__indicator,
.pul-checkbox__input:focus:checked ~ .pul-checkbox__indicator,
.pul-checkbox:hover .pul-checkbox__input:checked ~ .pul-checkbox__indicator {
  border-color: #0099ea;
  background-color: #0099ea;
}
.pul-checkbox__input:checked ~ .pul-checkbox__indicator::after {
  display: block;
  content: "";
  position: absolute;
  top: 0;
  left: 4px;
  width: 6px;
  height: 10px;
  border: solid #fff;
  border-width: 0 2px 2px 0;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.pul-checkbox__input[disabled] ~ .pul-checkbox__indicator,
.pul-checkbox:hover .pul-checkbox__input[disabled] ~ .pul-checkbox__indicator,
.pul-checkbox__input:checked[disabled] ~ .pul-checkbox__indicator,
.pul-checkbox:hover .pul-checkbox__input:checked[disabled] ~ .pul-checkbox__indicator {
  border-color: #e2e2e2;
  background-color: #f4f4f4;
  cursor: not-allowed;
}
.pul-checkbox__input[disabled] ~ .pul-checkbox__text,
.pul-checkbox:hover .pul-checkbox__input[disabled] ~ .pul-checkbox__text,
.pul-checkbox__input:checked[disabled] ~ .pul-checkbox__text,
.pul-checkbox:hover .pul-checkbox__input:checked[disabled] ~ .pul-checkbox__text {
  color: #999;
  cursor: not-allowed;
}
.pul-checkbox__input:checked[disabled] ~ .pul-checkbox__indicator::after {
  border-color: #999;
}



