/*
Alert

Markup:
<div class="alert .modifier">
  {{children}}
</div>

Modifiers:
.alert--success
.alert--warning
.alert--danger
.alert--info
*/
.pul-alert {
  padding: 10px;
  margin-bottom: 12px;
  background-color: #f0f0f0;
}
* + .pul-alert {
  margin-top: 12px;
}
.pul-alert .close {
  text-shadow: none;
  float: right;
  line-height: 16px;
  margin-left: 10px;
  text-align: center;
  cursor: pointer;
}
.pul-alert .close:hover {
  opacity: 0.8;
}
.pul-alert .close::before {
  content: "\2715";
  font-weight: 700;
  font-size: 13px;
}
.pul-alert .btn {
  margin: 0;
  vertical-align: inherit;
}
.pul-alert > p,
.pul-alert .b-indent > p,
.pul-alert > ul,
.pul-alert .b-indent > ul {
  margin-bottom: 0;
}
.pul-alert > p + p,
.pul-alert .b-indent > p + p,
.pul-alert > p + .b-indent,
.pul-alert .b-indent > p + .b-indent {
  margin-top: 8px;
}
.pul-alert .b-indent + .b-indent,
.pul-alert .b-indent + p {
  margin-top: 8px;
}
.pul-alert--success {
  background-color: #bcdf94;
}
.pul-alert--success hr {
  border-top-color: #a3d36d;
}
.pul-alert--info {
  background-color: #c6e5fa;
}
.pul-alert--info hr {
  border-top-color: #97d0f6;
}
.pul-alert--warning {
  background-color: #fdd896;
}
.pul-alert--warning hr {
  border-top-color: #fcc564;
}
.pul-alert--danger {
  background-color: #f4b3b3;
}
.pul-alert--danger hr {
  border-top-color: #ee8686;
}
.panel .pul-alert {
  padding: 10px 9px;
  margin-left: -10px;
  margin-right: -10px;
}
.panel .pul-alert .close::before {
  font-size: 11px;
}
.panel .pul-alert .btn {
  min-height: 0;
  padding: 3px 5px;
  font-size: inherit;
}



