@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  src: local("Open Sans Light"), local("OpenSans-Light"), url("../fonts/open-sans-300.woff2") format("woff2"), url("../fonts/open-sans-300.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: local("Open Sans"), local("OpenSans"), url("../fonts/open-sans-regular.woff2") format("woff2"), url("../fonts/open-sans-regular.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 600;
  src: local("Open Sans Semibold"), local("OpenSans-Semibold"), url("../fonts/open-sans-600.woff2") format("woff2"), url("../fonts/open-sans-600.woff") format("woff");
}
@font-face {
  font-family: "Open Sans Tilde";
  src: local("Arial");
  unicode-range: U+007E;
}
html {
  overflow-y: scroll;
  font-family: "Open Sans Tilde", "Open Sans", "Helvetica Neue", "Helvetica", sans-serif;
  font-size: 13px;
  font-weight: normal;
  line-height: 1.4;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  min-width: 100%;
}
/*rtl:raw:
    html { direction: rtl; }
    html * { unicode-bidi: embed; }
*/
html,
body {
  height: 100%;
  min-height: 100%;
}
body {
  margin: 0;
  background: #fff;
  color: #000;
}
article,
aside,
dialog,
figcaption,
figure,
footer,
header,
hgroup,
main,
nav,
section {
  display: block;
}
progress {
  vertical-align: baseline;
}
template,
[hidden] {
  display: none;
}
iframe {
  border: 0;
}
a,
area,
button,
input,
label,
select,
summary,
textarea {
  -ms-touch-action: manipulation;
      touch-action: manipulation;
}
hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
  margin: 12px 0;
  border: 0;
  border-top: 1px solid #ddd;
}
pre {
  padding: 10px;
  background: #f5f5f5;
  font: 12px/18px "Consolas", "Liberation Mono", "Menlo", "Courier", monospace;
  color: #444;
  -moz-tab-size: 4;
    -o-tab-size: 4;
       tab-size: 4;
  overflow: auto;
}
a {
  color: #2498e3;
  text-decoration: none;
  cursor: pointer;
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}
a:active,
a:hover {
  outline: none;
}
a:hover {
  color: #188dd9;
  text-decoration: underline;
}
[tabindex="-1"]:focus {
  outline: none;
}
b,
strong {
  font-weight: bolder;
}
code,
kbd,
samp {
  font-family: "Consolas", "Liberation Mono", "Menlo", "Courier", monospace;
  font-size: 12px;
  color: #d05;
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sub {
  bottom: -0.25em;
}
sup {
  top: -0.5em;
}
audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle;
}
audio,
canvas,
img,
video {
  box-sizing: border-box;
}
img {
  border-style: none;
}
svg:not(:root) {
  overflow: hidden;
}
input,
button,
select,
optgroup,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}
button,
input {
  overflow: visible;
}
button,
select {
  text-transform: none;
}
button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}
button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}
textarea {
  overflow: auto;
}
fieldset {
  padding: 0;
  margin: 0;
  border: 0;
}
legend {
  display: block;
  width: 100%;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}
ul,
ol {
  padding-left: 20px;
}
ul ul,
ul ol,
ol ol,
ol ul {
  margin: 0;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
td,
th {
  padding: 0;
}
p,
ul,
ol,
dl,
blockquote,
pre,
address,
fieldset,
figure {
  margin-top: 0;
  margin-bottom: 12px;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 12px;
  font-family: "Open Sans", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
  font-weight: normal;
  color: #444;
  text-transform: none;
}
h1 {
  font-size: 36px;
  line-height: 42px;
}
h2 {
  font-size: 24px;
  line-height: 30px;
}
h3 {
  font-size: 18px;
  line-height: 24px;
}
h4 {
  font-size: 16px;
  line-height: 22px;
}
h5 {
  font-size: 14px;
  line-height: 20px;
}
h6 {
  font-size: 12px;
  line-height: 18px;
}



