.pul-dialog__heading {
  padding: 10px 20px;
  line-height: 26px;
  background-color: #219ed4;
}
.pul-dialog__heading-title {
  margin: 0;
  font-size: 24px;
  color: #fff;
}
.pul-dialog__banner,
.pul-dialog__image {
  text-align: center;
}
.pul-dialog__banner-inner {
  width: 100%;
  height: auto;
}
.pul-dialog__image {
  padding-bottom: 10px;
}
.pul-dialog__image-inner {
  max-width: 100%;
  height: auto;
}
.pul-dialog__content {
  padding: 10px 20px;
  max-height: calc(100vh - 112px);
  overflow-y: auto;
}
.pul-dialog__buttons {
  margin-top: 12px;
  padding-top: 10px;
  border-top: 1px solid #ddd;
  text-align: center;
}
.pul-dialog__buttons .pul-button {
  min-width: 90px;
  margin-right: 10px;
  padding: 8px 12px;
  font-weight: 600;
}
@media (min-width: 480px) {
  .pul-dialog__content {
    padding-bottom: 0;
  }
  .pul-dialog__form-footer,
  .pul-dialog__buttons {
    position: -webkit-sticky;
    position: sticky;
    bottom: 0;
    background-color: white;
    padding-bottom: 10px;
  }
}



