.pul-menu {
  position: relative;
  min-width: 150px;
  max-width: 100%;
  margin: 0;
  padding: 5px 0;
  list-style: none;
  font-size: 13px;
  text-align: left;
  line-height: normal;
  background-color: #dedede;
  border: none;
  background-clip: padding-box;
  box-sizing: border-box;
}
.pul-menu__base-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 7px 10px;
  font-weight: normal;
  line-height: 16px;
  color: #373737;
  white-space: nowrap;
}
.pul-menu__base-item .pul-label {
  box-sizing: border-box;
  min-width: 16px;
  margin-left: 8px;
  padding: 1px 4px;
  vertical-align: top;
  border-radius: 2px;
}
.pul-menu__header {
  font-size: 13px;
  color: #777;
}
.pul-menu__item {
  cursor: pointer;
}
.pul-menu__item:hover,
.pul-menu__item:focus {
  text-decoration: none;
  outline: 0;
  color: #fff;
  background-color: #5aadd1;
}
.pul-menu__item--active,
.pul-menu__item--active:hover,
.pul-menu__item--active:focus {
  color: #fff;
  background-color: #5aadd1;
}
.pul-menu__item--disabled,
.pul-menu__item--disabled:hover,
.pul-menu__item--disabled:focus {
  color: #373737;
  opacity: 0.5;
  background: none;
  cursor: not-allowed;
}
.pul-menu__base-item-icon {
  color: #969696;
}
.pul-menu__item--active .pul-menu__base-item-icon,
.pul-menu__item:hover .pul-menu__base-item-icon,
.pul-menu__item:focus .pul-menu__base-item-icon {
  color: currentColor;
}
.pul-menu__base-item-content {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  overflow: hidden;
  text-overflow: ellipsis;
}
.pul-menu__divider {
  height: 1px;
  margin: 5px 10px;
  background-color: #b1b1b1;
}
.pul-menu--icons .pul-menu__base-item-icon {
  min-width: 30px;
}
.pul-menu--icons .pul-menu__divider {
  margin-left: 38px;
}
.pul-menu--icons::after {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 32px;
  width: 1px;
  height: 100%;
  content: "";
  background: #b1b1b1;
}



