/*
Tabs

Markup:
<span class="label">Default</span>

Modifiers:
.label--inactive
.label--info
.label--success
.label--warning
.label--danger
*/
.pul-label {
  display: inline-block;
  padding: 2px 6px;
  background: #80c2de;
  font-size: 12px;
  font-weight: 600;
  line-height: 14px;
  color: #fff;
  text-align: center;
  vertical-align: top;
  background-color: #80c2de;
}
a.pul-label:hover,
a.pul-label:focus {
  color: #fff;
  text-decoration: none;
  cursor: pointer;
}
.pul-label[href]:hover,
.pul-label[href]:focus {
  background-color: #58afd3;
}
.pul-label--inactive {
  background-color: #747474;
}
.pul-label--inactive[href]:hover,
.pul-label--inactive[href]:focus {
  background-color: #5a5a5a;
}
.pul-label--success {
  background-color: #86c939;
}
.pul-label--success[href]:hover,
.pul-label--success[href]:focus {
  background-color: #6ca32c;
}
.pul-label--info {
  background-color: #80c2de;
}
.pul-label--info[href]:hover,
.pul-label--info[href]:focus {
  background-color: #58afd3;
}
.pul-label--warning {
  background-color: #feb534;
}
.pul-label--warning[href]:hover,
.pul-label--warning[href]:focus {
  background-color: #fea301;
}
.pul-label--danger {
  background-color: #ed6060;
}
.pul-label--danger[href]:hover,
.pul-label--danger[href]:focus {
  background-color: #e83232;
}



