.pul-input {
  box-sizing: border-box;
  height: 26px;
  max-width: 100%;
  padding: 3px 6px 4px;
  border: solid #afbac1;
  border-width: 0 0 1px;
  background-color: #ededed;
  color: #020202;
  transition: all linear 0.2s;
}
.pul-input:focus {
  border-color: #0f7faf;
  outline: 0;
  background-color: #ecf5f9;
  color: #020202;
}
.pul-input[readonly] {
  border-color: #afbac1;
  background-color: #ededed;
  color: #020202;
}
.pul-input[disabled] {
  border-color: #e2e2e2;
  background-color: #f4f4f4;
  color: #999;
  opacity: 1;
}
.pul-input--size-sm {
  width: 50px;
}
.pul-input--size-md {
  width: 200px;
}
.pul-input--size-lg {
  width: 350px;
}
.pul-input--size-xl {
  width: 500px;
}
.pul-input--size-fill {
  width: 100%;
}



