.pul-figure {
  position: relative;
  display: inline-block;
}
.pul-figure--slide {
  margin: 0;
}
.pul-figure__caption {
  padding-top: 5px;
}
.pul-figure__overlay {
  position: absolute;
  padding: 16px;
}
.pul-figure__overlay--top {
  top: 0;
  right: 0;
  left: 0;
}
.pul-figure__overlay--bottom {
  right: 0;
  bottom: 0;
  left: 0;
}
.pul-figure__overlay--cover {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.pul-figure__action {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.8) 100%);
  cursor: pointer;
}
.pul-figure__action:hover {
  background-color: rgba(0, 0, 0, 0.4);
}
.pul-figure__action + .pul-figure__overlay {
  pointer-events: none;
  line-height: 1.3;
  font-weight: 300;
  font-size: 12px;
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.8);
}



