.pul-search-bar {
  width: 100%;
  position: relative;
}
.pul-search-bar__group {
  width: 100%;
  position: relative;
}
.pul-search-bar__group > .pul-search-bar__input {
  height: auto;
  transition: none;
}
.pul-search-bar__group > button:not(.pul-search-bar__search-button):not(:hover):not(:focus):not(:active) {
  border: none;
  border-bottom: 1px solid #afbac1;
  background-color: #ededed;
}
.pul-search-bar--focused .pul-search-bar__group > button:not(.pul-search-bar__search-button):not(:hover):not(:focus):not(:active) {
  border-bottom-color: #0f7faf;
  background-color: #ecf5f9;
}
.pul-search-bar:not(.pul-search-bar--focused) .pul-search-bar__search-button {
  border: none;
  margin-left: 0;
}
.pul-search-bar:not(.pul-search-bar--focused) .pul-search-bar__search-button:not(:hover):not(:focus):not(:active) {
  border-bottom: 1px solid #afbac1;
  background-color: #ededed;
}
.pul-search-bar__reset-button {
  position: absolute;
  right: 30px;
  top: 0;
  bottom: 0;
  color: #e12d2d;
}
.pul-search-bar__results {
  position: absolute;
  right: 0;
  z-index: 1035;
  margin: 2px 0;
  width: 100%;
}
.pul-search-bar__results--hidden {
  display: none;
}



